<template>
  <div>
    <div
      ref="submenu"
      :class="`fixed transition duration-500 ease-in-out w-full bg-white z-10 left-0 top-0 right-0 sub-header ${
        isMenuVisible ? 'sub-header--visible' : 'sub-header--hidden'
      }`"
      >
      <div
        ref="submenu"
        class="container flex py-3 justify-between"
      >
        <div class="logo">
          <router-link to="/"
            ><img src="@/assets/images/logo.svg"
          /></router-link>
        </div>
        <ul class="head-link flex space-x-4 items-center">
          <li
            class="cursor-pointer hover:text-blue transition duration-500 ease-in-out"
          >
            <router-link to="/projects">Projects</router-link>
          </li>
          <li
            class="cursor-pointer hover:text-blue transition duration-500 ease-in-out"
          >
            <router-link to="/journey">My Journey</router-link>
          </li>
          <li
            class="cursor-pointer hover:text-blue transition duration-500 ease-in-out"
          >
            <router-link to="/contact">Contact</router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="header-box"></div>
  </div>
</template>

<script>
export default {
  name: "Header",
  props: {
    msg: String,
  },
  data: () => ({
    isMenuVisible: true,
    isHamburgerOpen: false,
    lastScrollPosition: 0,
  }),

  watch: {
    isHamburgerOpen(isOpen) {
      document.body.classList[isOpen ? 'add' : 'remove']('overflow-y-hidden')
    },
  },
  methods: {
    onScroll() {
      // Get the current scroll position
      const currentScrollPosition =
        window.pageYOffset || document.documentElement.scrollTop
      // this.isMenuVisible = currentScrollPosition > 90
      if (currentScrollPosition > 90) {
        this.isMenuVisible = currentScrollPosition < this.lastScrollPosition
      } else {
        this.isMenuVisible = true
      }
      this.lastScrollPosition = currentScrollPosition
    },
  },
  mounted() {
    if (process.browser) {
      window.addEventListener('scroll', this.onScroll)
    }
  },
  beforeDestroy() {
    if (process.browser) {
      window.removeEventListener('scroll', this.onScroll)
    }
  },
};
</script>
