  <div>
    <div class="container">
      <Icon name="cross" />
      <section class="pt-24 w-full">
        <div class="flex flex-col justify-center">
          <div class="cover-role text-6xl font-serif"><span></span> Hi, I'm Vivek Singh</div>
          <div class="cover-role flex items-center">
            <span class="font-serif text-6xl mr-3">I work as</span>
            <vue-typer
              class="font-serif text-6xl text-gradient typed"
              :text="['Frontend Developer', 'UI/UX Developer', 'Web Developer']"
              :repeat="Infinity"
              :shuffle="true"
              initial-action="erasing"
              :pre-type-delay="70"
              :type-delay="70"
              :pre-erase-delay="2000"
              :erase-delay="100"
              erase-style="clear"
              :erase-on-complete="false"
              caret-animation="smooth"
            ></vue-typer>
          </div>
          <div class="cover-desc mt-6 md:w-2/3">
            <p class="font-pb text-xl">
              Over 7+ Years of Experience in UI Development &amp; Designing.
              Working As Frontend Developer/Web Developer/UI/UX Developer.
            </p>
          </div>
          <div class="mt-10">
            <Social />
          </div>
        </div>
      </section>
    </div>
    <section>
      <div class="container">
        <section class="flex flex-wrap justify-center mt-40">
          <div class="project-block sticky mb-20 w-11/12 max-w-full"
            v-for="(item, index) in project.featured" :key="item.id"
            @click="openProjectView(item)"
          >
            <ProjectCard
              :name="item.name"
              :image="item.image"
              :isOpen="index === 0" />
          </div>
        </section>
        <div class="flex justify-center">
          <router-link class="btn" to="/projects"
            >View all Projects</router-link
          >
        </div>
