<template>
  <div>
    <div class="container">
      <div class="py-20 text-center">
        <h2 class="text-6xl font-serif text-gradient">All Projects</h2>
        <p class="text-lg max-w-screen-md mx-auto">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Facilis explicabo adipisci at eius laboriosam repellat fugiat impedit iure labore, doloremque harum accusamus, minima fuga quo vel aspernatur amet iusto porro?
        </p>
      </div>
    </div>
    <div class="container">
      <section class="flex flex-wrap justify-between mt-30">
        <div class="p-card"
          v-for="(item, index) in project.all.slice().reverse()" :key="item.id"
          @click="openProjectView(item)"
        >
          <ProjectCard
            :name="item.name"
            :image="item.image"
            :isOpen="index === 0"
          />
        </div>
      </section>
    </div>
    <ProjectView :selectedProject="selectedProject" :customClass="[selectedProject ? 'reveal' : '']" :closeProjectView="closeProjectView" />
  </div>
</template>

<script>
import ProjectCard from "@/components/ProjectCard";
import ProjectView from "@/components/ProjectView";
import { project } from '../constants/project';

export default {
  name: "Projects",

  components: {
    ProjectCard,
    ProjectView
  },
  data() {
    return {
      project,
      selectedProject: null
    }
  },
  props: {
    items: {
      type: Array,
      required: true,
      default: () => [],
    },
  },
  methods :{
    openProjectView(item){
      this.selectedProject = item
    },
    closeProjectView(){
      this.selectedProject = null
    }
  },
};
</script>
